.phone {
  position: relative;
  display: block;
  padding-left: 30px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
  text-decoration: none;
  color: var(--basic-background);

  @media (min-width: $medium-width) {
    font-size: 24px;
    line-height: 24px;
  }

  @media (min-width: $large-width) {
    font-size: 30px;
    line-height: 30px;
  }

  &::before {
    display: block;
    position: absolute;
    top: 0;
    left: 100px;
    content: "";
    width: 20px;
    height: 20px;
    background-color: var(--basic-background);
    mask-image: url("../../img/icons/phone100.png");
    mask-repeat: no-repeat;
    mask-size: cover;
    mask-position: center center;
    transform: rotate(10deg);

    @media (min-width: $medium-width) {
      width: 35px;
      height: 35px;
      top: 8px;
      left: 270px;
    }

    @media (min-width: $large-width) {
      width: 40px;
      height: 40px;
      top: 10px;
      left: 575px;
    }
  }

  &--color::before {
    background-color: var(--basic-second);
  }

  &--color:hover::before {
    color: var(--addition);
  }

  &:hover {
    transform: scale(1.1);
    opacity: 0.8;
  }

  &--color:active::before {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.5;
  }
}
