.page {
  font-family: var(--basic-font), "Arial", sans-serif;
  text-transform: uppercase;
  color: var(--basic);
}

.page__leveler {
  margin: 0 auto;
  width: 320px;

  @media (min-width: $medium-width) {
    width: 768px;
  }

  @media (min-width: $large-width) {
    width: 1440px;
  }
}

.page__body {
  margin: 0;
  background-color: var(--background);
}
