.page-header {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $medium-width) {
    width: 672px;
  }

  @media (min-width: $large-width) {
    width: 1220px;
    padding: 0 50px;
    color: var(--basic-background);
    background-color: var(--basic-element);
  }
}

.page-header__identity {
  margin: 23px 0;

  @media (min-width: $medium-width) {
    margin-top: 57px;
    margin-bottom: 48px;
  }
}
