.contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 16px;
  text-transform: none;

  @media (min-width: $medium-width) {
    width: 672px;
    align-items: center;
  }

  @media (min-width: $large-width) {
    width: 1120px;
    margin-bottom: 50px;
  }
}

.contacts__address {
  margin-top: 14px;
  margin-bottom: 14px;
  width: 110px;

  @media (min-width: $medium-width) {
    width: 150px;
    font-size: 16px;
    line-height: 20px;
    margin: 0;
    padding-bottom: 48px;
  }
}

.contacts__phone {
  width: 110px;
  text-align: right;

  @media (min-width: $medium-width) {
    margin: 0;
  }

  @media (min-width: $large-width) {
    margin-right: -5px;
  }
}

.contacts__phone-link {
  margin-top: -25px;
  margin-right: 5px;
  font-size: 12px;
  line-height: 16px;
  font-weight: 700;
  text-decoration: none;
  color: var(--basic-second);

  &::before {
    top: 0;
    left: 10px;
    width: 25px;
    height: 25px;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    color: var(--addition);
  }

  @media (min-width: $medium-width) {
    font-size: 14px;

    &::before {
      top: -3px;
      left: 8px;
      width: 30px;
      height: 30px;
    }
  }

  @media (min-width: $large-width) {
    margin-top: -40px;
    color: var(--basic-background);

    &::before {
      background-color: var(--basic-background);
    }
  }
}
