.introduction__wrap {
  overflow: hidden;

  &::before {
    display: block;
    content: "";
    width: 320px;
    height: 216px;
    background-image: url("../../img/photo-1-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top left;

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/photo-1-mobile@2x.jpg");
    }

    @media (min-width: $medium-width) {
      width: 768px;
      height: 512px;
      background-image: url("../../img/photo-1-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-1-tablet@2x.jpg");
      }
    }

    @media (min-width: $large-width) {
      margin: 0 auto;
      width: 1220px;
      height: 800px;
      margin-top: 25px;
      background-image: url("../../img/photo-1-desktop@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-1-desktop@2x.jpg");
      }
    }
  }
}

.introduction__header {
  width: 320px;
  margin-top: -110px;
  margin-bottom: 10px;
  padding: 10px 30px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
  color: var(--basic-background);
  background-color: var(--addition);

  @media (min-width: $medium-width) {
    width: 768px;
    margin-top: -240px;
    padding: 10px 90px 10px 90px;
    font-size: 32px;
    line-height: 48px;
    letter-spacing: 2px;
  }

  @media (min-width: $large-width) {
    width: 1440px;
    margin: 0 auto;
    margin-top: -320px;
    font-size: 40px;
    line-height: 54px;
    padding: 30px 400px 30px 400px;
  }
}

.introduction__phone {
  width: 320px;
  margin: 0 auto;
  margin-top: -10px;
  padding-bottom: 25px;
  text-align: center;
  background-color: var(--addition);

  @media (min-width: $medium-width) {
    width: 768px;
    padding-top: 10px;
    padding-bottom: 50px;
  }

  @media (min-width: $large-width) {
    width: 1440px;
    margin-top: 0;
    padding-bottom: 50px;
  }
}

.introduction__title {
  margin: 0 auto;
  padding-top: 24px;
  padding-bottom: 30px;
  display: flex;
  justify-content: stretch;
  position: relative;

  &::before {
    flex-grow: 1;
    content: "";
    width: 80px;
    height: 2px;
    margin-top: 12px;
    margin-right: 10px;
    margin-left: 0;
    background-color: var(--img-background);
  }

  &::after {
    flex-grow: 1;
    content: "";
    width: 80px;
    height: 2px;
    margin-top: 12px;
    margin-right: 0;
    margin-left: 10px;
    background-color: var(--img-background);
  }

  @media (min-width: $medium-width) {
    padding: 72px 0;

    &::before,
    &::after {
      margin-top: 22px;
    }
  }

  @media (min-width: $large-width) {
    margin-top: 98px;
    margin-bottom: 172px;
    padding: 0;

    &::before,
    &::after {
      margin-top: 24px;
    }
  }
}

.introduction__list {
  padding: 0 16px;
  list-style: none;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  text-align: center;
  color: var(--basic);

  @media (min-width: $medium-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 658px;
    padding: 0;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
  }

  @media (min-width: $large-width) {
    width: 1220px;
    text-align: center;
  }
}

.introduction__item {
  padding-bottom: 30px;

  &:last-child {
    padding-bottom: 48px;
  }

  @media (min-width: $medium-width) {
    width: 320px;
    position: relative;
    padding-left: 70px;
    padding-bottom: 72px;

    &::before {
      position: absolute;
      display: block;
      content: "";
      top: 0;
      left: 0;
      width: 50px;
      height: 50px;
      opacity: 0.6;
      background-image: url("../../img/icons/check.svg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
    }

    &:last-child {
      margin: 0 auto;
      padding-bottom: 96px;
    }
  }

  @media (min-width: $large-width) {
    width: 340px;
    position: initial;
    padding: 0;
    margin-bottom: 144px;

    &::before {
      position: initial;
      margin-top: -72px;
      margin-bottom: 24px;
      margin-left: 145px;
    }

    &:last-child {
      margin: 0;
      padding-bottom: initial;
    }
  }
}
