.main-nav {
  position: relative;
  margin: 0 auto;
  min-height: 48px;

  @media (min-width: $medium-width) {
    min-height: auto;
    margin: 0;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  width: 272px;
  list-style: none;
  scroll-snap-type: y mandatory;

  @media (min-width: $medium-width) {
    width: 440px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
}

.main-nav__item {
  padding: 15px 0;
  text-align: center;
  vertical-align: middle;
  border-bottom: 1px solid var(--basic-element);
  scroll-snap-align: start;
  scroll-margin: 20px;

  &:first-child {
    border-top: 1px solid var(--basic-element);
  }

  &:last-child {
    border: none;
  }

  @media (min-width: $medium-width) {
    padding: 0;
    border: none;

    &:first-child {
      border: none;
    }
  }
}

.main-nav__link {
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  text-decoration: none;
  color: var(--basic);

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  &:focus-visible {
    outline: 2px solid var(--basic);
  }

  @media (min-width: $medium-width) {
    font-size: 14px;
    line-height: 18px;
  }

  @media (min-width: $large-width) {
    color: var(--basic-background);
  }
}

.main-nav__toggle {
  position: absolute;
  display: block;
  z-index: 2;
  width: 22px;
  height: 18px;
  top: -42px;
  right: 0;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;

  @media (min-width: $medium-width) {
    display: none;
  }
}

.main-nav--closed {
  min-height: 0;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 22px;
  height: 2px;
  background-color: var(--basic);
  box-shadow: 0 8px 0 0 var(--basic), 0 16px 0 0 var(--basic);
}

@media (max-width: $mobile-width-only) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    right: 0;
    width: 23px;
    height: 2px;
    background-color: var(--basic);
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}
