.social {
  width: 130px;

  @media (min-width: $medium-width) {
    width: 200px;
  }
}

.social__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.social__link {
  &::before {
    display: block;
    content: "";
    width: 18px;
    height: 18px;
    background-color: var(--basic-accent);
    mask-repeat: no-repeat;
    mask-size: cover;
    mask-position: center center;

    @media (min-width: $large-width) {
      background-color: var(--basic-background);
    }
  }

  &:hover::before {
    opacity: 0.6;
  }

  &:active::before {
    background-color: var(--addition);
  }

  &--twitter::before {
    width: 20px;
    height: 20px;
    mask-image: url("../../img/icons/twitter.svg");
  }

  &--fb::before {
    mask-image: url("../../img/icons/facebook.svg");
  }

  &--inst::before {
    mask-image: url("../../img/icons/instagram.svg");
  }

  &--wa::before {
    width: 20px;
    height: 20px;
    mask-image: url("../../img/icons/whatsapp.svg");
  }

  &--vk::before {
    mask-image: url("../../img/icons/vk.svg");
  }

  @media (min-width: $medium-width) {
    &::before {
      width: 30px;
      height: 30px;
    }

    &--twitter::before {
      width: 35px;
      height: 35px;
      mask-image: url("../../img/icons/twitter.svg");
    }

    &--wa::before {
      width: 35px;
      height: 35px;
      mask-image: url("../../img/icons/whatsapp.svg");
    }
  }
}
