:root {
  --basic: hsl(240 1% 12%);
  --basic-second: hsl(240 1% 35%);
  --basic-element: hsl(240 1% 60% / 0.8);
  --basic-accent: hsl(200 73% 49%);
  --basic-background: hsl(0 100% 100%);
  --img-background: hsl(200 73% 49% / 0.3);
  --background-accent: hsl(200 73% 49% / 0.8);
  --background: hsl(240 0% 90%);
  --addition: hsl(240 1% 12% / 0.6);
}
