.advantages {
  &::before {
    display: block;
    margin: 0 auto;
    content: "";
    width: 320px;
    height: 216px;
    background-image: url("../../img/photo-3-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/photo-3-mobile@2x.jpg");
    }

    @media (min-width: $medium-width) {
      width: 768px;
      height: 420px;
      background-image: url("../../img/photo-3-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-3-tablet@2x.jpg");
      }
    }

    @media (min-width: $large-width) {
      width: 1220px;
      height: 200px;
      margin-top: 24px;
      background-image: url("../../img/photo-3-desktop@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-3-desktop@2x.jpg");
      }
    }
  }

  &::after {
    display: block;
    margin: 0 auto;
    content: "";
    width: 320px;
    height: 216px;
    background-image: url("../../img/photo-4-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/photo-4-mobile@2x.jpg");
    }

    @media (min-width: $medium-width) {
      width: 768px;
      height: 420px;
      background-image: url("../../img/photo-4-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-4-tablet@2x.jpg");
      }
    }

    @media (min-width: $large-width) {
      width: 1220px;
      height: 200px;
      margin-bottom: 25px;
      background-image: url("../../img/photo-4-desktop@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-4-desktop@2x.jpg");
      }
    }
  }
}

.advantages__title {
  margin: 24px 0;
  display: flex;
  justify-content: stretch;
  position: relative;

  &::before {
    flex-grow: 1;
    content: "";
    width: 80px;
    height: 2px;
    margin-top: 12px;
    margin-right: 10px;
    margin-left: 0;
    background-color: var(--img-background);
  }

  &::after {
    flex-grow: 1;
    content: "";
    width: 80px;
    height: 2px;
    margin-top: 12px;
    margin-right: 0;
    margin-left: 10px;
    background-color: var(--img-background);
  }

  @media (min-width: $medium-width) {
    margin: 72px 0;

    &::before,
    &::after {
      margin-top: 22px;
    }
  }

  @media (min-width: $large-width) {
    margin-top: 120px;
    margin-bottom: 104px;
  }
}

.advantages__list {
  padding: 0 16px;
  list-style: none;

  @media (min-width: $medium-width) {
    width: 698px;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media (min-width: $large-width) {
    width: 1200px;
  }
}

.advantages__item {
  font-size: 14px;
  padding-bottom: 24px;
  line-height: 16px;
  text-align: center;

  &:last-child {
    padding-bottom: 48px;
  }

  @media (min-width: $medium-width) {
    position: relative;
    width: 340px;
    padding-left: 60px;
    padding-bottom: 72px;
    font-size: 16px;
    line-height: 24px;
    text-align: left;

    &::before {
      position: absolute;
      display: block;
      content: "";
      top: 5px;
      left: 0;
      width: 40px;
      height: 40px;
      opacity: 0.6;
      background-image: url("../../img/icons/checkmark.svg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
    }

    &:last-child {
      padding-bottom: 96px;
    }
  }

  @media (min-width: $large-width) {
    width: 430px;
  }
}
