.page-footer {
  @media (min-width: $medium-width) {
    width: 672px;
  }

  @media (min-width: $large-width) {
    width: 1220px;
    padding: 0 50px;
    color: var(--basic-background);
    background-color: var(--basic-element);
  }
}

.page-footer__identity {
  margin-top: 0;
  margin-bottom: 0;
  padding: 21px 0;
  text-align: center;
  border-bottom: 1px solid var(--basic-element);

  @media (min-width: $medium-width) {
    color: var(--basic-accent);
    border: none;
    margin-top: 55px;
    margin-bottom: 10px;
    padding: 0;
    text-align: left;
  }

  @media (min-width: $large-width) {
    margin-top: 20px;
    color: var(--basic-background);
  }
}

.page-footer__social-wrap {
  padding-top: 17px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid var(--basic-element);

  @media (min-width: $medium-width) {
    width: 440px;
    border: none;
    margin: 0;
    padding: 0;
  }

  @media (min-width: $large-width) {
    width: 650px;
  }
}

.page-footer__social {
  padding-bottom: 21px;

  @media (min-width: $medium-width) {
    width: 200px;
    margin-top: 50px;
    margin-bottom: 10px;
    padding: 0;
  }

  @media (min-width: $large-width) {
    margin-top: 100px;
  }
}

.page-footer__link {
  display: block;

  &::before {
    display: block;
    content: "";
    width: 30px;
    height: 30px;
    margin-top: -5px;
    background-color: var(--basic-accent);
    mask-image: url("../../img/icons/arrow30.png");
    mask-repeat: no-repeat;
    mask-size: cover;
    mask-position: center center;

    @media (min-width: $medium-width) {
      margin-top: 0;
      width: 130px;
      height: 24px;
      mask-image: url("../../img/icons/button.svg");
    }

    @media (min-width: $large-width) {
      background-color: var(--basic-background);
    }
  }

  &:hover::before {
    opacity: 0.6;
  }

  &:active::before {
    background-color: var(--addition);
  }

  @media (min-width: $medium-width) {
    margin-top: 60px;
  }
}

.page-footer__wrap {
  @media (min-width: $medium-width) {
    width: 672px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: $large-width) {
    width: 1120px;
  }
}
