.services {
  &::before {
    margin: 0 auto;
    display: block;
    content: "";
    width: 320px;
    height: 216px;
    background-image: url("../../img/photo-2-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/photo-2-mobile@2x.jpg");
    }

    @media (min-width: $medium-width) {
      width: 768px;
      height: 420px;
      background-image: url("../../img/photo-2-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-2-tablet@2x.jpg");
      }
    }

    @media (min-width: $large-width) {
      width: 1440px;
      height: 720px;
      background-image:
        linear-gradient(var(--background-accent), var(--background-accent)),
        url("../../img/photo-2-decktop@1x.jpg");
      background-repeat: no-repeat, no-repeat;
      background-position: center, center;
      background-size: cover, cover;

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/photo-2-decktop@2x.jpg");
      }
    }
  }
}

.services__title {
  margin: 24px 0;
  display: flex;
  justify-content: stretch;
  position: relative;

  &::before {
    flex-grow: 1;
    content: "";
    width: 80px;
    height: 2px;
    margin-top: 12px;
    margin-right: 10px;
    margin-left: 0;
    background-color: var(--img-background);
  }

  &::after {
    flex-grow: 1;
    content: "";
    width: 80px;
    height: 2px;
    margin-top: 12px;
    margin-right: 0;
    margin-left: 10px;
    background-color: var(--img-background);
  }

  @media (min-width: $medium-width) {
    margin: 72px 0;

    &::before,
    &::after {
      margin-top: 22px;
    }
  }

  @media (min-width: $large-width) {
    margin-top: -580px;
    margin-bottom: 162px;
    color: var(--basic-background);

    &::before,
    &::after {
      margin-top: 24px;
      background-color: var(--basic-background);
    }
  }
}

.services__list {
  padding: 0 16px;
  list-style: none;

  @media (min-width: $medium-width) {
    width: 658px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;
  }

  @media (min-width: $large-width) {
    width: 1220px;
    color: var(--basic-background);
  }
}

.services__item {
  position: relative;
  margin: 0;
  padding: 20px 0;
  padding-left: 85px;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;
  border-bottom: 1px solid var(--basic-element);

  &:last-child {
    padding-bottom: 48px;
    border-bottom: none;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 20px;
    left: 0;
    width: 52px;
    height: 44px;
    background-color: var(--basic-accent);
    mask-repeat: no-repeat;
    mask-size: cover;
    mask-position: center center;
  }

  &--warehouse::before {
    mask-image: url("../../img/icons/warehouse50.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/icons/warehouse100.png");
    }
  }

  &--carriage::before {
    height: 50px;
    mask-image: url("../../img/icons/carriage64.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/icons/carriage128.png");
    }
  }

  &--forklift::before {
    mask-image: url("../../img/icons/forklift64.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/icons/forklift128.png");
    }
  }

  &--rails::before {
    mask-image: url("../../img/icons/rails50.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/icons/rails100.png");
    }
  }

  @media (min-width: $medium-width) {
    width: 260px;
    font-size: 18px;
    line-height: 20px;
    padding-top: 120px;
    padding-left: 0;
    padding-bottom: 88px;
    border: none;
    text-align: center;

    &::before {
      left: 30%;
      top: 0;
      width: 115px;
      height: 96px;
    }

    &:first-child::before {
      left: 25%;
    }

    &:last-child {
      padding-bottom: 124px;
    }

    &--warehouse::before {
      width: 110px;
      mask-image: url("../../img/icons/warehouse100.png");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/icons/warehouse200.png");
      }
    }

    &--carriage::before {
      height: 99px;
      mask-image: url("../../img/icons/carriage128.png");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/icons/warehouse200.png");
      }
    }

    &--forklift::before {
      mask-image: url("../../img/icons/forklift128.png");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/icons/forklift256.png");
      }
    }

    &--rails::before {
      width: 120px;
      mask-image: url("../../img/icons/rails100.png");

      @media (min-resolution: 2dppx) {
        background-image: url("../../img/icons/rails200.png");
      }
    }
  }

  @media (min-width: $large-width) {
    width: 280px;
    margin-bottom: 190px;
    padding-bottom: 0;


    &::before {
      background-color: var(--basic-background);
    }

    &:last-child {
      padding-bottom: 0;
    }
  }
}
